header {
  font-size: 1.25rem;
  min-height: 4rem;
  padding: 0.5rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  color: white;
  z-index: 2;
}

header h1 {
  font-size: 1.5rem;
  margin: 0;
}

header a {
  color: white;
  padding: 0.5rem 1rem;
  border: 1px solid transparent;
  border-radius: 0.2rem;
}

header a:hover {
  text-decoration: none;
  border: 1px solid white;
}

header a.cart-link {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

header .cart-icon {
  background-color: white;
  color: var(--secondary-color);
  display: flex;
  gap: 0.5rem;
  align-items: center;
  border: 1px solid white;
  border-radius: 1.2rem;
  padding: 0.2rem 0.8rem;
}

@media (max-width: 650px) {
  header {
    position: relative;
    display: grid;
    grid-template: repeat(2, max-content) / repeat(3, max-content);
    justify-content: space-evenly;
    padding: 0.5rem;
    column-gap: 0.5rem;
  }

  header a {
    padding: 0.5rem;
  }

  header a:first-child {
    grid-column: 1 / span 3;
  }
}
